/* 

HEADER 

*/

.header {
  width: 100vw;
  height: 100px;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: right;
  color: #125488;
}

.logo {
  margin-left: 50px;
  left: 50px;
  position: absolute;

}

.navButtons {
  display: flex;
  justify-content: center;
  margin-right: 100px;
  
}

.navButtons button {
  font-size: 20px;
  font-weight: bold;
  margin-left: 20px;
  margin-right: 20px;
  border-width: 3px;
  border-color: transparent;
  background: transparent;
  color: #125488;
}

.navButtons button:hover {
  cursor: pointer;
  color: #d1ebff;
  transition: 200ms linear;

  border-bottom: 3px solid #125488 ;
}


/*

BODY

*/

.body {
  display: flex;
  flex-wrap: wrap;
  min-width: 100vw;
  min-height: 100vh;
  justify-content: center;

}

/* .intro {
 
} */

.pfp {
  width: 500px;
  height: 500px;
  border-radius: 250px;
  margin-top: 15vh;
  margin-bottom: 10vh;
}


.particles {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  position: fixed;
 
}

.nameText {
  font-size: 80px;
  width: 45vw;
  margin-top: 15vh;
  color: #125488;
}

.nameText_Name {
  color: #ff8b3d;
}

.animatedText {
  font-size: 40px;
  width: 40vw;
  margin-top: 50px;
  margin-bottom: 25px;
  color: #d1ebff;
}

.aboutMeButton {
  width: 150px;
  height: 50px;
  background-color: #2a93d5;
  color: #d1ebff;
  font-weight: bold;
  border-radius: 25px;
  border-width: 0px;
  font-size: 20px;
}

.aboutMeButton:hover {
  background-color: #37caec;
  cursor: pointer;
}

.socials {
  display: flex;
  width: 40vw;
  margin-top: 20px;
 
}

.socials button {
  width: 60px;
  height: 60px;
  color: #d1ebff;
  background-color: #d1ebff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  border-width: 0px;
  margin-right: 10px;
}

.socials button:hover {
  color: #d1ebff;
}




.projects {
  width: 100vw;
  min-height: 100vh;
  background-color: transparent;
  text-align: center;
  padding: 100px;
  color: #fff;
  font-size: 20px;
}

.projects h1 {
  margin: 50px;
  font-size: 40px;
}

.projects span {
  width: 100%;
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgba(32, 32, 32, 0);
  border-radius: 50px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.projects span span {
  width: 600px;
  height: 400px;
  margin: 50px;
  padding: 10px;
  display: inline-block;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(0, 0, 0, 0);
  background-color: transparent;
  transition: all 0.25s ease-in-out;
}

.projects_name {
  font-size: 24px;
  font-weight: bold;
}

.projects span span:hover {
  color:#fff;
  
  opacity: 1;
}

.projects p {
  z-index: 1;
}

.projects-img {
  position: absolute;
  opacity: 100;
  transition: all 0.25s ease-in-out;
}

.projects-img:hover {
  cursor: pointer;
  opacity: 0.8;
  
}

.about {
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.about_box {
  width: 1000px;
  height: 600px;
  font-size: 20px;
  text-align: center;
  background-color: transparent;
  padding: 20px;
  display: flex;
  text-align: left;
  border-width: 0px;
  border-color: #ff9822;
  border-radius: 50px;
  border-style: solid;
}

.about_box span {
  width: 500px;
  height: 500px;
  margin: 20px;
}

.scrollButton {
  width: 40px;
  height: 40px;
  background-color: #ff9d5c;
  color: #fff ;
  border-radius: 30px;
  border-width: 0px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 120px;
  right: 50px;
}

.scrollButtonFalse {
  visibility: hidden;
}

.scrollButton:hover {
  background-color: #ff8b3d;
  cursor: pointer;
}

.scrollButton p {
  margin-right: 10px;
}

.contact {
  width: 100vw;
  min-height: 720px;
  background-color: transparent;
  padding: 50px;
  justify-content: center;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.contact h1 {
  margin: 50px;
  width: 100%;
  color: #fff;
}

.contact div {
  min-width: 400px;
  width: 800px;
  height: 600px;
  background-color: #d1ebff;
  margin: 50px;
  display: flex;
  align-self: center;
  border-radius: 50px;
  
}


/*

FOOTER

*/

.footer {
  width: 100vw;
  height: 100px;
  background-color: #125488;
  margin-bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #d1ebff;
}

.footerButton {
  margin-left: 10px;
  margin-right: 10px;
  width: 50px;
  height: 50px;
  border-radius: 30px;
  border-color: #000;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footerButton:hover {
  background-color: #37caec;
  cursor: pointer;
}

.footerIcon {
  margin-left: 10px;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  color: #d1ebff;
}

.footerIcon:hover {
  cursor: pointer;
  color: #37caec;
}

/*

PROJECT

*/

.project {
  width: 90vw;
  min-height: 80vh;
  margin-left: 5vw;
  margin-top: 100px;
  margin-bottom: 100px;
  border-radius: 50px;
  background-color: rgba(31, 31, 31, .7);
  padding: 50px;
  color: #fff;

}

.project img {
  min-width: 100%;
  
}





/*

ABOUT

*/

.aboutpage {
  width: 100vw;
  min-height: 100vh;
  border-radius: 5px;
  background-color: transparent;
  padding: 50px;
  color: #fff;
}

.sectionTitle{
  margin-top: 50px;
  
  text-align: center;
}

.aboutpage h1 {
  margin: 50px;
  text-align: center;
  font-size: 40px;
}


.skills {
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 20px;
  padding-top: 50px;
  padding-bottom: 50px;
  margin-bottom: 50px;
  flex-wrap: wrap;
  text-align: center;
  background-color: rgba(32, 32, 32, 0);
  border-radius: 50px;

}

.skill {
  width: 200px;
  height: 150px;
  margin: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  text-align: center;
  padding: 25px;
  background-image: linear-gradient(-45deg, #125488, #2a93d5);
  border-radius: 20px;
}

.skill span {
  margin: 10px;
  width: 100%;
}

